import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function targetConsensus(tableName: string) {
  const title = "Average Price Target"
  return genericColumn({
    endpoint: "companyPriceTarget",
    columnKeys: ["targetConsensus"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Gives a benchmark of what brokers/analysts believe is the stock's fair value.",
    extraFinanceData: entityIds => ({ companyCurrency: { entityIds } }),
    cell: info => {
      const currency = info.row.original.financialData.companyCurrency?.data?.tradingCurrency
      return (
        <WatchlistDataTablePriceCell
          queryResult={info.row.original.financialData.companyPriceTarget}
          tooltip={"Explain the Target Consensus"}
          currency={currency}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `Explain how recent market trends could contribute to ${info.row.original.entityInfo.name} base-case scenario, potentially resulting in the target consensus.`,
              value: info.row.original.financialData.companyPriceTarget?.data?.targetHigh,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyPriceTarget?.data?.targetConsensus}
        />
      )
    },
  })
}

export { targetConsensus }
